import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { Clock } from "lucide-react";

const TimePicker = () => {
  const [fromTime, setFromTime] = useState("09:00");
  const [toTime, setToTime] = useState("10:45");
  const [open, setOpen] = useState(false);

  const generateTimeOptions = () => {
    const options = [];
    for (let hour = 0; hour < 24; hour++) {
      for (let minute = 0; minute < 60; minute += 15) {
        const time = `${hour.toString().padStart(2, "0")}:${minute
          .toString()
          .padStart(2, "0")}`;
        options.push(time);
      }
    }
    return options;
  };

  const timeOptions = generateTimeOptions();

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          className="w-[200px] justify-start text-left font-normal"
        >
          <Clock className="mr-2 h-4 w-4" />
          {fromTime} ➔ {toTime}
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-auto p-4">
        <div className="flex flex-col space-y-4">
          <div className="flex items-center justify-between">
            <div className="flex-1">
              <h4 className="text-sm font-medium mb-2">From</h4>
              <select
                value={fromTime}
                onChange={(e) => setFromTime(e.target.value)}
                className="w-full p-2 border rounded"
              >
                {timeOptions.map((time) => (
                  <option key={`from-${time}`} value={time}>
                    {time}
                  </option>
                ))}
              </select>
            </div>
            <div className="px-4 pt-6">➔</div>
            <div className="flex-1">
              <h4 className="text-sm font-medium mb-2">To</h4>
              <select
                value={toTime}
                onChange={(e) => setToTime(e.target.value)}
                className="w-full p-2 border rounded"
              >
                {timeOptions.map((time) => (
                  <option key={`to-${time}`} value={time}>
                    {time}
                  </option>
                ))}
              </select>
            </div>
          </div>
          <Button onClick={() => setOpen(false)} className="mt-4">
            Apply
          </Button>
        </div>
      </PopoverContent>
    </Popover>
  );
};

export default TimePicker;
